<!-- @format -->

<template>
  <div class="userCreate">
    <h1></h1>
    <h2></h2>
    <el-form label-position="top" label-width="80px" :model="user">
      <el-row :gutter="20">
        <el-col :xs="24" :sm="12">
          <el-form-item label="">
            <el-input v-model="name"></el-input>
          </el-form-item>
        </el-col>
        <el-col :xs="24" :sm="12">
          <el-form-item label="">
            <el-select v-model="roleName" placeholder="">
              <el-option
                v-for="item in rolesOptions"
                :key="item.name"
                :label="item.name"
                :value="item.name"
              >
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :xs="24" :sm="12">
          <el-form-item label="">
            <el-input v-model="account"></el-input>
          </el-form-item>
        </el-col>
        <el-col :xs="24" :sm="12">
          <el-form-item label="">
            <div class="password">000</div>
          </el-form-item>
        </el-col>
      </el-row>
      <el-form-item label="">
        <el-row :gutter="20">
          <el-col :xs="17" :sm="20">
            <el-form-item>
              <el-select v-model="company" placeholder="" value-key="id">
                <el-option
                  v-for="item in companiesOptions"
                  :key="item.id"
                  :label="item.name"
                  :value="item"
                >
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :xs="7" :sm="4">
            <el-button @click="toPath('Companies')"></el-button>
          </el-col>
        </el-row>
      </el-form-item>
      <el-form-item label="" v-if="isShow('account:soItemSelf')">
        <el-select v-model="soItem" placeholder="" value-key="id">
          <el-option
            v-for="item in soItemOptions"
            :key="item.id"
            :label="item.number"
            :value="item"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button @click="toPath('Users')">
          
        </el-button>
        <el-button type="primary" @click="onSubmit">
          
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import ToPathMixin from '@/mixins/ToPath'
export default {
  name: 'UserEdit',
  mixins: [ToPathMixin],
  data() {
    return {
      isShowPassword: false
    }
  },
  computed: {
    user() {
      return this.$store.getters.currentUser
    },
    name: {
      get() {
        return this.$store.getters.currentUser.name
      },
      set(value) {
        this.$store.commit('currentUserName', value)
      }
    },
    roleName: {
      get() {
        return this.$store.getters.currentUser.roleName
      },
      set(value) {
        this.$store.commit('currentUserRoleName', value)
      }
    },
    rolesOptions() {
      return this.$store.getters.roles
    },
    account: {
      get() {
        return this.$store.getters.currentUser.account
      },
      set(value) {
        this.$store.commit('currentUserAccount', value)
      }
    },
    password() {
      const key = this.$store.getters.currentUser.password
      // eslint-disable-next-line
      return (this.isShowPassword) ? key : key.split('').map(() => '*').join('')
    },
    companiesOptions() {
      return this.$store.getters.companies
    },
    company: {
      get() {
        return this.$store.getters.currentUser.company
      },
      set(value) {
        this.$store.commit('currentUserCompany', value)
      }
    },
    soItem: {
      get() {
        return this.$store.getters.currentUser.soItem
      },
      set(value) {
        this.$store.commit('currentUserSoItem', value)
      }
    },
    soItemOptions() {
      return this.$store.getters.soItemsFree
    }
  },
  methods: {
    onSubmit() {
      this.$store
        .dispatch('createUser')
        .then(() => {
          this.$message({
            message: ` ${this.user.name}`,
            type: 'success',
            center: true,
            duration: 1800
          })
          this.toPath('Users')
        })
        .catch(e => {
          this.$message.error(` ${e.message}`)
        })
    },
    isShow(feature) {
      return this.$store.getters.currentUserPermissions
        .filter(permission => permission.value)
        .map(permission => permission.name)
        .includes(feature)
    }
  }
}
</script>
<style lang="scss" scoped>
.password {
  font-family: monospace;
}
</style>
