<!-- @format -->

<template lang="html">
  <div class="measuresSo">
    <h1></h1>
    <el-form label-position="top">
      <section>
        <h2></h2>
        <p> {{ project.number }}</p>
        <p> {{ project.name }}</p>
        <p>: {{ soItem.number }}</p>
      </section>
      <section>
        <h2></h2>
        <el-form-item label="" required>
          <el-select
            v-model="projectPhaseId"
            placeholder=" 1 "
            @change="clearMeasuresDatas"
          >
            <el-option
              v-for="projectPhase in projectPhaseOptions"
              :key="projectPhase.id"
              :label="projectPhase.name"
              :value="projectPhase.id"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" required>
          <el-select
            v-model="soLocationNumber"
            placeholder="SO-01"
            @change="onChangeLocation"
          >
            <el-option
              v-for="location in project.soLocation"
              :key="location.number"
              :label="location.number"
              :value="location.number"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=" IP" required>
          <el-input
            v-model="wiseIP"
            @change="clearMeasuresDatas"
            placeholder="192.168.58.200"
          >
          </el-input>
        </el-form-item>
        <span> {{ currentDepth }} </span>
        <el-form-item
          label="  1m "
          label-width="300px"
        >
        </el-form-item>
        <el-button
          @click="measures"
          :disabled="
            measuresSoDatas.length >= currentDepth ||
              !projectPhaseId ||
              isMeasuring ||
              !soItem
          "
        >
          <span v-if="isMeasuring"></span>
          <span v-else></span>
        </el-button>
      </section>
      <section v-if="measuresSoDatas.length">
        <p>
           {{ currentDepth }} {{ measuresSoDatas.length }}
        </p>
        <el-table :data="measuresSoDatas">
          <el-table-column fixed type="index" width="50"> </el-table-column>
          <el-table-column prop="date" label="" width="100">
          </el-table-column>
          <el-table-column prop="time" label="" width="100">
          </el-table-column>
          <el-table-column prop="depth" label="(m)" width="80">
          </el-table-column>
          <el-table-column prop="temp" label="(c)" width="150">
          </el-table-column>
          <el-table-column prop="VoltageX" label="" width="120">
          </el-table-column>
          <el-table-column prop="degreeX" label="(C)" width="150">
          </el-table-column>
          <el-table-column prop="displacement" label="(cm)" width="150">
          </el-table-column>
          <el-table-column
            prop="totalDisplacement"
            label="(cm)"
            width="150"
          >
          </el-table-column>
        </el-table>
        <el-button @click="clearMeasuresDatas"></el-button>
        <el-button
          @click="uploadMeasuresDatas"
          :disabled="measuresSoDatas.length < currentDepth || isSend"
        >
          <span v-if="isSend"></span>
          <span v-else></span>
        </el-button>
      </section>
    </el-form>
  </div>
</template>

<script>
import startMeasures from '@/utils/measuresSo'
export default {
  data() {
    return {
      wiseIP: '192.168.58.200',
      measuresSoDatas: [],
      projectPhaseId: '',
      soLocationNumber: '',
      // soItem: {},
      isSend: false,
      isMeasuring: false,
      currentSoLocationIndex: -1
    }
  },
  methods: {
    measures: function() {
      let racePromise = new Promise(resolve => {
        setTimeout(() => {
          resolve('Promise A win!')
        }, 5000)
      })

      let raceMeasures = startMeasures(
        this.wiseIP,
        this.measuresSoDatas,
        this.soItem,
        this.currentDepth
      )
      if (!this.isMeasuring) {
        Promise.race([racePromise, raceMeasures])
          .then(() => {
            this.isMeasuring = false
          })
          .catch(() => {
            this.isMeasuring = false
          })
        this.isMeasuring = true
      }
    },
    clearMeasuresDatas: function() {
      this.measuresSoDatas = []
      this.isSend = false
    },
    uploadMeasuresDatas: function() {
      let measuresData = {
        projectId: this.$route.params.projectId,
        projectPhaseId: this.projectPhaseId,
        soLocationNumber: this.soLocationNumber,
        soItemId: this.soItem.id,
        soItemParameters: this.soItem.parameters,
        measureResult: this.measuresSoDatas
      }
      this.$store.dispatch('uploadMeasuresDatas', measuresData)
      this.isSend = true
    },
    onChangeLocation: function() {
      let filterResult
      filterResult = this.project.soLocation.filter(item => {
        return item.number === this.soLocationNumber
      })
      this.currentSoLocationIndex = this.project.soLocation.indexOf(
        filterResult.shift()
      )
      this.clearMeasuresDatas()
    }
  },
  computed: {
    project() {
      return this.$store.getters.project
    },
    projectPhaseOptions() {
      return this.$store.getters.projectPhases
    },
    me() {
      return this.$store.getters.me
    },
    currentDepth() {
      return ~this.currentSoLocationIndex
        ? this.project.soLocation[this.currentSoLocationIndex].depth
        : 0
    },
    soItem() {
      return this.$store.getters.mySoItem
    }
  },
  beforeDestroy() {
    if (window.location.protocol === 'https:') {
      window.location.protocol = 'https:'
    }
  }
}
</script>
