<!-- @format -->

<template>
  <el-form :model="me">
    <h1></h1>
    <section>
      <h2></h2>
      <p>{{ me.name }}</p>
      <p>{{ me.roleName }}</p>
      <p>{{ me.company.name }}</p>
      <p v-if="me.soItem"> {{ me.soItem.number }}</p>
    </section>

    <h2></h2>
    <el-form-item
      label=""
      prop="phone"
      :rules="[
        {
          required: true,
          pattern: /^[0][9]\d{8}$/,
          message: ' : 09xxxxxxxx',
          trigger: 'blur'
        }
      ]"
    >
      <el-input v-model="phone" placeholder=""> </el-input>
    </el-form-item>
    <el-form-item
      label="email"
      prop="email"
      :rules="[
        {
          required: true,
          message: ' email',
          trigger: 'blur'
        },
        {
          type: 'email',
          message: ' example@chuen.com.tw',
          trigger: ['blur', 'change']
        }
      ]"
    >
      <el-input v-model="email" placeholder=" email"> </el-input>
    </el-form-item>

    <h2></h2>
    <el-form-item label="">
      <el-checkbox v-model="isText" style="float:right;"></el-checkbox>
      <el-input
        v-model="password"
        :type="passwordShowType"
        placeholder=""
      >
      </el-input>
    </el-form-item>

    <el-form-item class="button-container">
      <el-button type="primary" @click="submit">
        
      </el-button>
    </el-form-item>
  </el-form>
</template>

<script>
// @ is an alias to /src
import ToPathMixin from '@/mixins/ToPath'

export default {
  name: 'UserInfo',
  mixins: [ToPathMixin],
  data() {
    return {
      isText: false
    }
  },
  computed: {
    me() {
      return this.$store.getters.me
    },
    phone: {
      get() {
        return this.$store.getters.myPhone
      },
      set(phone) {
        this.$store.commit('myPhone', phone)
      }
    },
    email: {
      get() {
        return this.$store.getters.myMail
      },
      set(mail) {
        this.$store.commit('myMail', mail)
      }
    },
    passwordShowType() {
      return this.isText ? 'text' : 'password'
    },
    password: {
      get() {
        return this.$store.getters.myPassword
      },
      set(password) {
        this.$store.commit('myPassword', password)
      }
    }
  },
  methods: {
    updateMe(payload) {
      this.$store.commit('updateMe', payload)
    },
    submit() {
      this.$store
        .dispatch('updateMe')
        .then(() => {
          this.$message({
            message: '',
            type: 'success'
          })
          this.toPath('Projects')
        })
        .catch(e => {
          this.$message.error(` ${e.message}`)
        })
    }
  }
}
</script>
